@font-face {
  font-family: 'pretendard';
  src: url('../public/fonts/Pretendard-Medium.ttf') format('truetype');
}

/* 모든 웹 브라우저를 위한 기본 스타일 */
body, html {
  height: 100%;
  overflow-y: scroll; /* 스크롤 가능하게 설정 */
}

/* 웹킷 기반 브라우저(크롬, 사파리, 새 버전의 오페라)를 위한 스타일 */
::-webkit-scrollbar {
  display: none; /* 스크롤바 숨기기 */
}

.App {
  text-align: center;
  font-family: 'pretendard';
}

.image-section {
  position: relative;
  height: 100vh; 
  /* background-size: 100% 100%; 배경 이미지가 섹션을 완전히 채우도록 함 */
  background-size: 100% 100%; /* 배경 이미지의 비율을 유지하면서 섹션을 채움 */
  background-repeat: no-repeat; /* 이미지 반복 방지 */
  background-position: center; /* 이미지를 중앙에 위치시킴 */
  image-rendering: -moz-crisp-edges; /* firefox */
  image-rendering: -o-crisp-edges; /* opera */
  image-rendering: -webkit-optimize-contrast; /* chrome */
  image-rendering: crisp-edges;
  backface-visibility: hidden;
}

