.slider {
  position: absolute;
  top: 1150px;
  display: flex;
  align-items: center;
  justify-content: center; /* 중앙 정렬 */
  overflow: hidden; 
  width: 100%; /* 슬라이더의 너비를 조정합니다. */
  height: 470px; /* 슬라이더의 높이를 조정합니다. */
}

.slider img {
  position: absolute;
  width: 640px;
  height: 360px;
  transition: all 0.8s ease-in-out;
  opacity: 0.3;
}

/* 활성화된 이미지에 대한 스타일 */
.slider img.active {
  opacity: 1;
  z-index: 2;
  left: 50%; /* 가운데 정렬 */
  top: 50%; /* 상단에서부터 50%의 위치 */
  transform: translate(-50%, -50%); /* 정확하게 중앙에 배치 */
}

/* 이전 및 다음 이미지에 대한 스타일 */
.slider img.prev {
  z-index: 1;
  left: 25%; /* 중앙에서 왼쪽으로 조금 */
  top: 50%;
  transform: translate(-50%, -50%) scale(0.8);
}

.slider img.next {
  z-index: 1;
  left: 75%; /* 중앙에서 오른쪽으로 조금 */
  top: 50%;
  transform: translate(-50%, -50%) scale(0.8);
}

/* 나머지 이미지에 대한 스타일 */
.slider img.other {
  z-index: 0;
  left: 0; /* 왼쪽 또는 오른쪽 끝으로 이동 */
  top: 50%;
  transform: translate(-50%, -50%) scale(0.6);
}

