@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes bounce {
  0%, 100% {
    transform: translateX(-50%) translateY(-20px); /* 원래 위치 */
    animation-timing-function: ease-in; /* 위로 올라갈 때 속도가 빨라짐 */
  }
  50% {
    transform: translateX(-50%) translateY(5px); /* 아래로 5px 이동 */
    animation-timing-function: ease-out; /* 아래로 떨어질 때 속도가 느려짐 */
  }
}